header {
  color: #fff;
  z-index: 10;
  background-color: #c43e4a;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  padding: 0 40px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.leftHeader {
  align-items: center;
  height: 100%;
  display: flex;
}

.logo {
  object-fit: contain;
  width: auto;
  height: auto;
  max-height: 60px;
}

.rightHeader a {
  text-decoration: none;
}

.logout_btn {
  color: #c43e4a;
  background: #fff;
  border: none;
  border-radius: 25px;
  padding: 12px 32px;
  font-size: 16px;
  font-weight: 600;
}

.logout_btn:hover {
  background-color: #f0f0f0;
}

button {
  color: #000;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #aaa;
  border-radius: 5px;
  width: 100%;
  height: 100%;
  font-family: Epilogue-Regular, sans-serif;
  font-size: 20px;
}

button:hover {
  background-color: #f0f0f0;
}

body {
  background: #f3f3f3;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

main {
  max-width: 1400px;
  margin-top: 80px;
  margin-left: auto;
  margin-right: auto;
  padding: 40px 20px;
}
